<template>
   <div class="bill_list">
     <div class="deliverable_inline clearfix">
       <el-col :span="8">
        <div style="width: 80%;">
          <label for="">项目名称</label>
          <el-input placeholder="项目名称" v-model="form.project_name"></el-input>
        </div>
      </el-col>
      <el-col :span="8">
       <div style="width: 80%;">
         <label for="">订单号</label>
        <el-input placeholder="订单号" v-model="form.id"></el-input>
       </div>
     </el-col>
   </div>
   <div class="deliverable_inline clearfix">
     <el-col :span="16">
       <div style="width: 80%;">
         <label for="">交易日期</label>
         <el-date-picker
           v-model="form.starttime"
           type="date"
           placeholder="选择日期">
         </el-date-picker>
         <span>至</span>
         <el-date-picker
           v-model="form.endtime"
           type="date"
           placeholder="选择日期">
         </el-date-picker>
       </div>
     </el-col>
     </div>
     <div class="buttons">
       <el-button class="query" type="primary" @click="query">查询</el-button>
       <el-button class="export" type="primary">导出</el-button>
     </div>
     <div class="deliverable_table">
       <el-table
         :data="tableData"
         border
         style="width: 100%">
         <el-table-column
            align="center"
            prop="id"
            label="订单号"
            show-overflow-tooltip
            width="100">
         </el-table-column>
         <el-table-column
            align="center"
            prop="project_name"
            label="项目名称"
            show-overflow-tooltip
            width="150">
         </el-table-column>
         <el-table-column
            align="center"
            prop="service_name"
            label="服务名称"
            show-overflow-tooltip>
         </el-table-column>
         <el-table-column
            align="center"
            prop="price"
            label="金额"
            show-overflow-tooltip
            width="150">
         </el-table-column>
         <el-table-column
            align="center"
            prop="gmt_create"
            label="下单时间"
            show-overflow-tooltip
            width="150">
         </el-table-column>
       </el-table>
     </div>
     <v-pages :total="total" v-on:currentChange="query"></v-pages>
   </div>
</template>

<script>
import pages from '../components/pages/pages.vue'
export default {
  data () {
    return {
      form: {
        project_name: '',
        id: '',
        starttime: '',
        endtime: ''
      },
      tableData: [],
      total: 1,
      loading: false
    }
  },
  created() {
    this.query(1)
  },
  methods: {
    query(page) {
      var _this = this
      var page
      if (typeof page != 'object') {
        page = page
      } else {
        page = 1
      }
      if (this.form.starttime === '') {
        this.form.starttime = ''
      } else {
        this.form.starttime = Date.parse(new Date(this.form.starttime))
      }
      if (this.form.endtime === '') {
        this.form.endtime = ''
      } else {
        this.form.endtime = Date.parse(new Date(this.form.endtime))
      }
      $.ajax({
        url: '/admin/api/v1/bills?id='+this.form.id+'&project_name='+this.form.project_name+'&starttime='+this.form.starttime+'&endtime='+this.form.endtime+'&page=' + page,
        beforeSend: function() {
          _this.loading = true
        },
        success: function(result) {
          _this.loading = false
          var data = result.result
          _this.total = data.total
          for (var i in data.items) {
            data.items[i].gmt_create = data.items[i].gmt_create.split('T')[0]
          }
          _this.tableData = data.items
        }
      })
    }
  },
  components: {
    'v-pages': pages
  }
}
</script>

<style lang="scss">
.bill_list {
  .deliverable_inline {
    margin-bottom: 10px;
    label {
      display: block;
      margin-bottom: 5px;
    }
    .el-select {
        width: 100%;
    }
  }
  .buttons {
    margin: 30px 0;
    text-align: center;
  }
}
</style>



