<script>
import upload from '../assets/js/upload'
  export default {
    data() {
      return {
        right: 'right',
        fileList: [],
        fileType: '.doc,.docx,.ppt,.pptx,.xls,.xlsx,.txt,.rtf,.ppt,.bmp,.png,.jpg,.jpeg,.zip,.prt,.stp,.dxf,.dwg,.sch,.pcb,.dsn,.brd',
        form: {
          "bp_url": "",
          "business_mode": "",
          "ceo": "",
          "city": "",
          "cmo": "",
          "comment": "",
          "company_name": "",
          "contact": "",
          "contact_phone": "",
          "contact_title": "",
          "core_resource": "",
          "core_tech": "",
          "cto": "",
          "customers_resource": "",
          "description": "",
          "dest_customers": "",
          "employees": "",
          "financing_plan": "",
          "financing_sum": "",
          "full_time": "",
          "future_aim": "",
          "future_plan": "",
          "income_status": "",
          "industry": "",
          "industry_resource": "",
          "investors": "",
          "main_income": "",
          "market_capacity": "",
          "market_proficiency": "",
          "market_rate": "",
          "needs_desc": "",
          "needs_support": "",
          "organization": "",
          "our_resource": "",
          "pain_point": "",
          "potential_income": "",
          "product_status": "",
          "project_name": "",
          "recent_plan": "",
          "risk": "",
          "risk_detail": "",
          "rival": "",
          "score_evaluation": "",
          "score_industry": "",
          "score_mode": "",
          "score_needs": "",
          "score_product": "",
          "score_resource": "",
          "score_risk": "",
          "score_team": "",
          "source": "",
          "start_from": "",
          "stock_structure": "",
          "tags": "",
          "team_desc": "",
          "tech_evaluation": "",
          "timestamp": "",
          "valuation": ""
        },
        fileData: {},
        id: ''
      }
    },
    created() {
      this.id = this.$route.query
      //  根据id获取详情
      var _this = this
      if (typeof this.id === 'object') {
        this.$router.push('/admin/bp_list')
      } else {
        $.ajax({
          url: '/admin/api/v1/bps/' + this.id,
          success: function(result) {
            var data = result.result
            _this.form.start_from = data.gmt_create.split('T')[0]
            Object.assign(_this.form, data)
          }
        })
      }
    },
    methods: {
      //  上传
      uploadFile(ele) {
        var _this = this
        upload(ele.target, () => {
          _this.form.bp_url = $("#hiddens").val()
        })
      },
      //  修改BP
      submitBP() {
        var _this = this
        if (this.form.start_from === '') {
          this.form.start_from = ''
        } else {
          this.form.start_from = Date.parse(new Date(this.form.start_from))
        }
        if (this.form.bp_url === "0") {
          this.$message.error('没有获取到上传url')
        } else {
          $.ajax({
            url: '/admin/api/v1/bps/' + this.id,
            type: 'post',
            contentType: 'application/json',
            data: JSON.stringify(this.form),
            success: function(result) {
              _this.$message({
                message: result.message,
                type: 'success'
              })
              _this.$router.push('/admin/bp_list')
            }
          })
        }
      }
    }
  }
</script>

<style lang="scss">
.bp_manage {
  .xs {
    width:500px;
    height:50px;
    background:#fff;
    position: fixed;
    top:-50px;
    left:50%;
    margin-left:-250px;
    z-index:4;
    border-radius:6px;
    border:1px solid #efefef;
    line-height:50px;
    padding-left:15px;
  }
}
</style>



